import popup from '@/components/basis/popup.vue';
export default {
	components: {
		popup
	},
	data() {
		return {
			title: 'Hello',
			showSpecifications: false, //支付弹窗控制
			userlist:'',
			quantity:'',
			method:[
				{
					name:'银行转账',
					id:1
				},
				{
					name:'微信支付',
					id:2
				},
				{
					name:'线下自理',
					id:3
				}
				
			],
			Selected:'',//用户已选择支付方式
			setAddr:''//用户已选择地址
		}
	},
	onLoad() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		if (this.setAddr!='') {
			this.setAddr ='';
		} else{
			return false;
		}
	},
	onShow() {
		this.setAddr = uni.getStorageSync('setAddr'); //加载选中地址缓存
		console.log(this.setAddr);
	},
	methods: {
		// 支付弹窗
		SpecificationsShow() {
			this.showSpecifications = true;
		},
		
		SpecificationsClose() {
			this.showSpecifications = false;
		},
		serverItemClick(index) {
			for (let i = 0; i < this.method.length; i++) {
				let item = this.method[i];
				item.active = index == i ? true : false;
				this.method.splice(i, 1, item);
				if (index == i) {
					this.Selected = item;
				}
			}
		},
		serverButton() {
			for (let i = 0; i < this.method.length; i++) {
				if (this.method[i].active == true) {
					this.showSpecifications = false;
				}
			}
		},
		tijiao(){
			let _this = this;
			if(_this.quantity==''|| _this.quantity<30){
				_this.utils.error('请输入不小于30的进货数量!！');return;
			}
			if(!_this.Selected){
				uni.showToast({
					title: "请选择支付方式！",
					icon: "none",
					duration: 3000
				})
				return;
			}
			_this.separate();
			
		},
		separate(){
			let _this = this;
			_this.utils.showloading();
			if (_this.userlist.spareThree <600 || _this.userlist.spareThree == 0) {
					_this.http.getApi('Companyorder/found', {
								storeId: _this.userlist.id,
								parentId: _this.userlist.storeParentid,
								storePhone:_this.setAddr.receiverMobile,
								storeRole:_this.userlist.storeRole,
								quantity:_this.quantity,
								paymentType:_this.Selected.id,
								addressid:_this.setAddr.id,
								storeName:_this.userlist.storeUsername								
							}, 'POST',_this.userlist.storeOpendid).then(res => {
								console.log(res);
								uni.hideLoading();
								_this.updateUser(_this.userlist.storeOpendid);
								_this.utils.success('提交成功',()=>{
									_this.utils.navback();
								});
							}).catch(err => {
								uni.hideLoading();
								uni.showToast({
									title: err.msg,
									icon: "none",
									duration: 3000
								})
							});
			} else{
					_this.http.getApi('/order/found', {
								storeId: _this.userlist.id,
								parentId: _this.userlist.storeParentid,
								storePhone:_this.setAddr.receiverMobile,
								storeRole:_this.userlist.storeRole,
								quantity:_this.quantity,
								paymentType:_this.Selected.id,
								spareTwo:_this.setAddr.id
							}, 'POST',_this.userlist.storeOpendid).then(res => {
								console.log(res);
								uni.hideLoading();
								if (res.data.storeRole==5) {
									uni.showModal({
										title: '您的总进货量已达到58万',
										content: '为了保证您的合法权益请打款到公司，电话：0851-27659188',
										success: function(res) {
											if (res.confirm) {
												_this.updateUser(_this.userlist.storeOpendid);
												_this.utils.success('提交成功',()=>{
													_this.utils.navback();
												});
											} else if (res.cancel) {
												_this.updateUser(_this.userlist.storeOpendid);
												_this.utils.success('提交成功',()=>{
													_this.utils.navback();
												});
											}
										}
									});
								} else{
									_this.updateUser(_this.userlist.storeOpendid);
									_this.utils.success('提交成功',()=>{
										_this.utils.navback();
								});
								}
								
							}).catch(err => {
								uni.hideLoading();
								console.log(err);
								uni.showToast({
									title: err.msg,
									icon: "none",
									duration: 3000
								})
							});
			}
		},
		// 更新用户信息
		updateUser: function(data) {
			// 判断是否登录
			this.http
				.getApi('/user/login', { storeLogin: 1, storeOpendid: data }, 'post', '')
				.then(res => {
					console.log('updateUser更新用户信息');
					uni.setStorageSync('userlist', res.data);
				})
				.catch(err => {
				});
		}
	}
}
